  /* HS집토끼체 */
  @font-face {
    font-family: 'HSJiptokki-Black';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/HSJiptokki-Black.woff2') format('woff2');
    font-weight: 900;
    font-style: normal;
  }
  
  /* 라인시드체 */
  @font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
  }
  
  /* 디자인하우스체 */
  @font-face {
    font-family: 'designhouseOTFLight00';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_three@1.0/designhouseOTFLight00.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  }

  .Common-all {
    text-align: center;
    word-break: keep-all;
  }

  .Common-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .Common-body {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .Common-footer {
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: black;
  }

  .Common-top-title {
    font-family: 'HSJiptokki-Black';
    font-size: 7vmin;
    margin-left: 10px;
    margin-right: 10px;
  }

  .Common-sub-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
  }
  
  .Common-sub-title {
    font-family: 'designhouseOTFLight00';
    font-size: 6vmin;
    background: linear-gradient(to top, #353841 35%, transparent 35%);
    /* background: linear-gradient(to top, #FFE400 35%, transparent 35%); */
  }
  
  .Common-sub-content {
    font-family: 'LINESeedKR-Bd';
    font-size: 4vmin;
  }

  .Common-empty-area {
    height: 50px;
  }
  
  .Common-link {
    color: #61dafb;
    font-family: 'LINESeedKR-Bd';
    font-size: 4vmin;
  }